// QuoteCard component
import MyButton from "./ui/button";
import { Copy, Share2, Check } from "lucide-react";

export default function QuoteCard({ quote, onCopy, onShare, copied }) {
  return (
    <div className="card w-full max-w-2xl bg-white dark:bg-gray-800 shadow-xl hover:shadow-2xl transition-shadow duration-300 rounded-xl overflow-hidden mb-6">
      <div className="card-body p-8">
        <blockquote className="relative">
          <div className="absolute -left-4 top-0 text-5xl text-gray-300 dark:text-gray-600">"</div>
          <p className="italic text-lg md:text-xl pl-6 pr-2 text-gray-800 dark:text-gray-200">
            {quote.content}
          </p>
          <div className="absolute -right-2 bottom-0 text-5xl text-gray-300 dark:text-gray-600">"</div>
        </blockquote>
        
        <p className="text-right mt-4 text-gray-600 dark:text-gray-400 font-medium">
          — {quote.author}
        </p>
        
        <div className="card-actions flex justify-between mt-6">
          <MyButton 
            onClick={onCopy} 
            className={`flex items-center ${copied ? 'bg-success hover:bg-success' : ''}`}
          >
            {copied ? (
              <>
                <Check className="mr-2 w-4 h-4" /> Copied!
              </>
            ) : (
              <>
                <Copy className="mr-2 w-4 h-4" /> Copy
              </>
            )}
          </MyButton>
          
          <MyButton 
            onClick={onShare}
            variant="outline"
            className="flex items-center border border-primary text-primary hover:bg-primary/10"
          >
            <Share2 className="mr-2 w-4 h-4" /> Share
          </MyButton>
        </div>
      </div>
    </div>
  );
}